.title-hover {
   font-size: 38px;
   -webkit-animation: dubstep 1.5s infinite ease-in-out;
           animation: dubstep 1.5s infinite ease-in-out;
}

@-webkit-keyframes dubstep {
    0% {
        text-shadow: 0 0 0 #fff;
    }
    40% {
        text-shadow: 3px 0 0 #da7b93;
    }
    43% {
        text-shadow: 3px 0 0 #2E151B,
        15px 0 0 #da7b93,
        -7px 0 0 #376e6f;
        color: #f1f1f1;
    }
    44% {
        text-shadow: 3px 0 0 #2E151B,
        15px 2px 0 #da7b93,
        -7px -1.5px 0 #376e6f;
        color: rgba(0, 0, 0, 0.2);
    }
    45% {
        text-shadow: 3px 0 0 #2E151B,
        15px 0 0 #da7b93,
        -7px 0 0 #376e6f;
        color: #f1f1f1;
    }
    60% {
        text-shadow: 3px 0 0 #2E151B,
        20px 0 0 #da7b93,
        -15px 0 0 #376e6f;
        color: #f1f1f1;
    }

    100% {
        text-shadow: 0 0 0 #fff;
    }
}

@keyframes dubstep {
    0% {
        text-shadow: 0 0 0 #fff;
    }
    40% {
        text-shadow: 3px 0 0 #da7b93;
    }
    43% {
        text-shadow: 3px 0 0 #2E151B,
        15px 0 0 #da7b93,
        -7px 0 0 #376e6f;
        color: #f1f1f1;
    }
    44% {
        text-shadow: 3px 0 0 #2E151B,
        15px 2px 0 #da7b93,
        -7px -1.5px 0 #376e6f;
        color: rgba(0, 0, 0, 0.2);
    }
    45% {
        text-shadow: 3px 0 0 #2E151B,
        15px 0 0 #da7b93,
        -7px 0 0 #376e6f;
        color: #f1f1f1;
    }
    60% {
