figure.snip0015 h2 {
    word-spacing: -0.15em;
    font-weight: 300;
    text-transform: uppercase;
    -webkit-transform: translate3d(0%, 50%, 0);
    transform: translate3d(0%, 50%, 0);
    transition-delay: 0.3s;
}

figure.snip0015 h2 span {
    font-weight: 800;
}

figure.snip0015 p {
    font-weight: 200;
    transition-delay: 0s;
}

.carousel-cell.is-selected > figure.snip0015 a {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    color: #ffffff;
}

.carousel-cell.is-selected > figure.snip0015:hover img {
    opacity: .35;
}

figure.snip0015:hover figcaption h2 {
    opacity: 1;
    -webkit-transform: translate3d(0%, 0%, 0);
    transform: translate3d(0%, 0%, 0);
    transition-delay: 0.3s;
}

figure.snip0015:hover figcaption p {
    opacity: 0.9;
    transition-delay: 0.6s;
}

.carousel-cell.is-selected > figure.snip0015:hover figcaption::before {
    background: rgba(0, 0, 0, 0.5);
    top: 30px;
    bottom: 30px;
    opacity: 1;
    transition-delay: 0s;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

